@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}
body {
    line-height: 1;
}
ol,
ul {
    list-style: none;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

* {
    font-family: "Jost", sans-serif;
}

:root {
    --primary: #470bd3;
    --secondary: #320992;
    --tertiary: #240669;
    --primaryBackground: #09090a;
    --secondaryBackground: #151720;
    --tertiaryBackground: #0d0d11;
    --primaryText: #b4b4b4;
    --secondaryText: #797979;
    /* --primary: #ff009d;
    --secondary: #d10081;
    --tertiary: #860053;
    --primaryBackground: #ffffff;
    --secondaryBackground: #e9e9e9;
    --tertiaryBackground: #dbdbdb;
    --primaryText: #0c0c0c;
    --secondaryText: #555555; */
}

body {
    background-color: #09090a;
    background-color: var(--primaryBackground);
}

body::-webkit-scrollbar {
    display: none;
}

h1,
h2 {
    color: #b4b4b4;
    color: var(--primaryText);
}
p {
    color: #b4b4b4;
    color: var(--primaryText);
}
.page {
    padding: 4rem;
    box-sizing: border-box;
    flex: 1 1;
    height: 100%;
}

.navbar {
    height: 100%;
    position: fixed;
    right: 0;
    top: 0;
    /* width: 4rem; */
    background-color: #151720;
    background-color: var(--secondaryBackground);

    display: flex;
    flex-direction: column;
}

.navbar-option {
    display: block;
    width: "100%";
    height: auto;
    font-size: 3rem;
    text-align: center;
    padding: 1rem;
    color: #470bd3;
    color: var(--primary);

    transition: all 200ms ease;
    text-decoration: none !important;
}

.navbar-option > h1 {
    font-size: 1.5rem;
}

.navbar-option:hover {
    color: #320992;
    color: var(--secondary);
}

.navbar-option:active {
    color: #240669;
    color: var(--tertiary);
}

.phrase-card {
    background-color: #151720;
    background-color: var(--secondaryBackground);

    border-radius: 1rem;
    /* border-top: 2px solid gray;
    border-left: 2px solid gray;
    border-bottom: 2px solid 	black;
    border-right: 2px solid 	black; */

    padding: 1rem;
    box-sizing: border-box;

    margin-bottom: 2rem;
}

.phrase-card-text {
    /* font-weight: bold; */
    font-size: 1.25rem;
    color: #b4b4b4;
    color: var(--primaryText);
}

.phrase-card-author {
    text-align: right;
    font-size: 1rem;
    color: #797979;
    color: var(--secondaryText);
    margin-top: 1rem;
    width: 100%;
    display: block;
    text-decoration: none;
}

.phrase-card-author:hover {
    text-decoration: underline;
}

.phrase-card-actions {
    width: 100%;

    margin-top: 1rem;

    /* display: flex;
    flex-direction: row;
    justify-content: flex-start; */
}

.phrase-card-action {
    font-size: 1rem;
    padding: 0.5rem;

    background-color: transparent;
    background-color: #0d0d11;
    background-color: var(--tertiaryBackground);
    border: none;
    border-radius: 0.5rem;

    margin-right: 1rem;
}

.phrase-card-action:hover {
    opacity: 0.7;
}

.phrase-card-action:active {
    opacity: 0.4;
}

.phrase-card-tag {
    display: inline-block;
    background-color: #0d0d11;
    background-color: var(--tertiaryBackground);
    padding: 0.5rem;
    margin-right: 1rem;
    margin-bottom: 1rem;
    border-radius: 0.5rem;
    color: #797979;
    color: var(--secondaryText);
}

.phrase-column {
    flex: 1 1;
    /* height: auto; */

    /* display: grid; */
    /* justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    align-items: flex-start; */

    padding: 1rem;
    box-sizing: border-box;
    grid-gap: 2rem;
    gap: 2rem;
}

.phrase-column > h2 {
    font-size: 2rem;
    margin-bottom: 2rem;
}

input[type="text"],
input[type="password"] {
    background-color: #151720;
    background-color: var(--secondaryBackground);
    border: none;
    outline: none;

    font-size: 2rem;
    padding: 1rem;
    border-radius: 1rem;
    width: 100%;

    box-sizing: border-box;

    color: #b4b4b4;

    color: var(--primaryText);

    margin-bottom: 2rem;
}

button {
    transition: all 200ms ease;
    cursor: pointer;
}

button[type="submit"],
.form-button {
    opacity: 1 !important;

    background-color: #151720;

    background-color: var(--secondaryBackground);
    border: none;
    outline: none;

    color: #b4b4b4;

    color: var(--primaryText);
    font-size: 2rem;
    font-weight: bold;
    padding: 1rem;
    border-radius: 1rem;

    box-sizing: border-box;

    background-color: #470bd3;

    background-color: var(--primary);
}

button[type="submit"]:hover,
.form-button:hover {
    background-color: #320992;
    background-color: var(--secondary);
}

button[type="submit"]:active,
.form-button:active {
    background-color: #240669;
    background-color: var(--tertiary);
}

button:hover {
    opacity: 0.7;
}

button:active {
    opacity: 0.3;
}

.input-label {
    font-size: 2rem;
    margin-bottom: 2rem;
}

.input-error:before {
    content: "‚ùó";
    margin-right: 1rem;
    margin-left: 1rem;
}
.input-error {
    font-size: 1.5rem;
    margin-top: 1rem;
    margin-bottom: 2rem;
    color: rgb(235, 42, 42);
}

.page-heading {
    font-size: 5rem;
    margin-bottom: 2rem;
}

